import {Injectable} from '@angular/core';
//import {Router} from '@angular/router';
import { Response } from '@angular/http';
import { Http} from '@angular/http';
import { Observable }     from 'rxjs/Rx';
//import { Observable } from 'rxjs';
import { Headers, RequestOptions } from '@angular/http';


@Injectable()
export class AuthenticationService {

    constructor(private http: Http){}

	login (userData: any): Observable<any> { 

		    //let url  = 'http://validate.jsontest.com';
		    let url = 'http://localhost/blma/j_spring_security_check'; 
		    let json = JSON.stringify(userData);
		    //let json = userData;
		  	
		    let body =  json;  //'json=' +
		    console.log("Data to be send: ",body);
		    let headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded'}); 
		    let options = new RequestOptions({ headers: headers });

		    console.log("Final Data to be send to server: ",body);

		    return this.http.post(url, body, options) //body insted of json
		                    .map(this.extractData)
		                    .catch(this.handleError);
		  }

	getData(){
		    let url = 'http://localhost:8080/blma/role/admin'; 		 
		    return this.http.get(url) 
		                    .map(this.extractData)
		                    .catch(this.handleError);	
	}	  

	 private extractData(res: Response) {
		    let body = res.json();	

		    console.log('In service response--', res ); //+JSON.stringify(body)	

		    return body || { };//
		  }

	  private handleError (error: any) {
			    // In a real world app, we might use a remote logging infrastructure
			    // We'd also dig deeper into the error to get a better message
			    let errMsg = (error.message) ? error.message :
			      error.status ? `${error.status} - ${error.statusText}` : 'Server error';
			    console.error(errMsg); // log to console instead
			    return Observable.throw(errMsg);
			  }

  	/*logout() {
		    localStorage.removeItem("user");
		    this._router.navigate(['Login']);
		  }
 
    checkCredentials(){	
		    if (localStorage.getItem("user") === null){
		        this._router.navigate(['Login']);
		    }
		  } */
	
}



