
import {Component, Inject, OnInit} from "@angular/core";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
//import './rxjs-operators';
import { Router }            from '@angular/router';
import { Observable }        from 'rxjs/Observable';
import { Subject }           from 'rxjs/Subject';
import { AuthenticationService }  from './authentication.service';


@Component({
    selector: 'authentication',
    template: require('./login.html'),
    styles  : [`

body{
  background-color: #1F89CE;
}
.center{
  text-align: center;
}

.loginFormInput{
  height: 50px;
  border-radius: 0px;
}

.loginHeader{
  color: #1976D2;
}

.card{
  margin-top: 100px;
  background-color: white;
  box-shadow: 1px box-shadow;
  -webkit-box-shadow: 1px 1px 2px 0px rgba(107,97,107,1);
  -moz-box-shadow: 1px 1px 2px 0px rgba(107,97,107,1);
  box-shadow: 1px 1px 2px 0px rgba(107,97,107,1);
}

.float-left{
  float: left;
}

`],
    providers: [ AuthenticationService]
})



export class AuthenticationComponent{

     form: FormGroup;
     userData: any;
     loginResponse: any;
     errorMessage: any;
     authFail: boolean; 
     counter: number = 3;
     blockUser: boolean;
     resInString: any;
     resInObj: any;
     startCounterForFailAuth: any;
     disableBtn: any;
     authFailCounter:number = 3;
     display:boolean = false;
     getResponse: any;


     constructor(private fb: FormBuilder, private authenticationService: AuthenticationService, private router : Router) {

         this.form = this.fb.group({
          name: this.fb.group({
            j_username: ['', Validators.minLength(2)],
            j_password: ''
            //,
            //keepMelogedIn : '',
          }),

         // userName : '',
        //  password: '',
        });     

         // row ok // alert(this.form.value.name.userName);

        this.userData = this.form.value.name;
        console.log("This is userData to be send to server for authentication: ", JSON.stringify(this.userData));  // row ok //

       // row ok // console.log("Counter is", this.counter+1 );

       // row ok // this.router.navigate(['/dashboard']);

      }

 ngOnInit() {

       if(this.router.url == "/login"){
          document.body.style.backgroundColor = "#2399E5";
       }           
  }


   
    //  selectedValues: string[] = [];
    // selectedValues: string[] = ['val1','val2'];

    //  let username: string = "";

    //  public user = new User('','');
    // public errorMsg = '';
 


   login() {
      this.authenticationService.login(this.form.value.name)
                     .subscribe(
                       data  =>  { 

                                  this.loginResponse = data  //JSON.stringify();

                                  console.log("Sever response ", this.loginResponse);

                                  if(this.loginResponse.object_or_array == "object"){
                                        this.authFail = true;
                                        this.authFailCounter--;

                                        if(this.authFailCounter < 1)
                                          {
                                             // alert("You have only "+ this.counter +" attempts 
                                             //remaining to login.");

                                              this.counter--;
                                              this.display = true;

                                              if(this.counter == 0){
                                                this.disableBtn = true;
                                              }

                                          }
                                  }
                                  else{
                                    console.log("this is object", JSON.stringify(this.loginResponse.object_or_array));
                                  }


                       }, 
                       error =>  alert(error)
                       );  

                       //this.errorMessage = <any>error);
                       //console.log(this.loginResponse);
  }

      getData(){

              console.log("in get Data function");

               this.authenticationService.getData()
                     .subscribe(
                       data  =>  { 

                                  this.getResponse = data  

                                  console.log("Sever get response ", this.getResponse);

                       }, 
                       error =>  alert(error)
                       );  
      }




   logout() {
    //    this._service.logout();
    }
}



